import { Component ,React} from "react";
import {Grid,Paper,Button,TextField,Checkbox} from "@material-ui/core";
import "./join.css"
class Join extends Component{
	
    constructor(props){
        super(props);
        this.state = {name:undefined,room:undefined,public:false}
    }
    Join_room = ()=>{
        if (this.state.public && !this.state.name) return;
        else if (!this.state.public && (!this.state.name || !this.state.room)) return;
        else if (this.state.room === "public" && !this.state.public){
            alert("invalid room name");
            return;
        }
        this.props.history.push({
           pathname: "/room",  
            room: this.state.public?"public":this.state.room,
            name: this.state.name
          });

    }
    changeName=(e)=>{
        this.setState(st=>({...st,name:e.target.value}))
    }
    handleCheck = ()=>{
        this.setState((st)=>({...st,public:!st.public}))
    }
    render(){
        return(<div className={"field_container"}>
            <Paper  className="Paper_style" elevation={3}>
                <Grid className="Grid_style" item container direction={"column"}>
                    <TextField style={{margin:".5rem"}} id={"name_inp"} value={this.state.name} onChange={this.changeName} label={"name"}/>
                    {!this.state.public?<TextField style={{margin:".5rem"}} id={"room_inp"} value={this.state.room}  onChange={e=>{this.setState({...this.state,room:e.target.value})}} label={"room id"}/>:null}
                    {!this.state.public?"The room will be created if it doesn`t exist":null}
                    <div><Checkbox
                        color={"primary"}
                        checked={this.state.public}
                        onChange={this.handleCheck}
                        inputProps={{ 'aria-label': 'primary checkbox' }}
                        />enter public chat</div>
                    <Button style={{margin:"0.5rem"}} variant="contained" color="primary" onClick={this.Join_room}>
                        Join room
                    </Button>
                </Grid>
        </Paper>
        </div>)
    }

}

export default Join;

