import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import loadable from '@loadable/component';import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from "@material-ui/core/CircularProgress"
import Join from "./pages/join";

const App = () => {
  const Chat =loadable(() => import('./pages/chat'),{fallback: <Backdrop open><CircularProgress/></Backdrop>});
  return (
    <BrowserRouter>
      <Switch>
<Route exact path="/room" component={Chat}/>

<Route component={Join} />      </Switch>
    </BrowserRouter> 
  );
};

export default App;
